@import url(https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@500&display=swap);
* {
  margin: 0;
}

body {
  margin: 0;
  font-family: "Cormorant Garamond", serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  display: flex;
  background-color: #ebf7fd;
}

/* .sidebar {
  width: 25%;
}

.page {
  width: 75%;
} */

@media (max-width: 500px) {
  .App {
    display: block;
  }
}

.home {
  color: #233142;
  width: 100%;
}

.home__container {
  width: 50%;
  margin: auto;
  margin-top: 375px;
}

.home__container > h1 {
  font-size: 2.5rem;
}

.home__container > p {
  font-size: 1.1rem;
  font-weight: 800;
  line-height: 1.85;
  text-align: justify;
}

.home__paragraph {
  text-indent: 50px;
  margin-top: 50px;
}

.home__salutation {
  margin-top: 75px;
}

@media (max-width: 500px) {
  .home__container {
    width: 80%;
    margin-top: 120px;
    padding-bottom: 50px;
    height: 100%;
  }

  .home__container > h1 {
    font-size: 28px;
  }

  .home__container > p {
    font-size: 16px;
  }
}

@media (max-width: 400px) {
  .home__container {
    width: 80%;
    margin-top: 80px;
    padding-bottom: 20px;
    height: 100%;
    line-height: 1.5;
  }

  .home__container > h1 {
    font-size: 24px;
  }

  .home__container > p {
    font-size: 14px;
  }
}

.projects {
  width: 100vw;
}

.projects__container {
  width: 100vw;
  margin: auto;
  margin-top: 100px;
  margin-left: 50px;
}

.project__set {
  margin: auto;
  margin-top: 15px;
}

@media (max-width: 500px) {
  .projects {
    width: auto;
    margin-top: 36px;
  }

  .projects__container {
    width: auto;
    margin-left: 0;
    margin-top: 0;
  }

  h1 {
    font-size: 20px;
  }
}

.project {
  width: 100%;
  padding: 5px 10px 5px 10px;
}

.project__title {
  font-size: 24px;
  font-weight: 800;
}

.project__link {
  display: block;
  color: black;
  text-decoration: none;
}

.project__link:visited {
  display: block;
  color: black;
  text-decoration: none;
}

.project__link:-webkit-any-link {
  display: block;
  color: black;
  text-decoration: none;
}

.project__link:any-link {
  display: block;
  color: black;
  text-decoration: none;
}

.project__image {
  width: 360px;
  height: 128px;
  object-fit: fill;
  border-radius: 5px;
}

@media (max-width: 500px) {
  .project {
    padding: none;
    margin: auto 32px auto 0;
  }
  .project__title {
    font-size: 18px;
  }

  p {
    font-size: 12px;
  }

  .project__image {
    width: 332px;
    height: 128px;
  }
}

@media (max-width: 400px) {
  .project__title {
    font-size: 16px;
  }

  .project__image {
    width: 256px;
    height: 64px;
  }
}

.sidebar {
  background-color: #233142;
  color: #ebf7fd;
  height: 100vh;
  width: 40%;
  text-align: center;
  letter-spacing: 2px;
}

.sidebar__image {
  padding: 5px;
  width: 200px;
  border-radius: 50%;
  background-color: white;
  margin-top: 100px;
}

.sidebar__title {
  margin-top: 50px;
  padding: 10px;
  font-size: 28px;
}

.sidebar__options {
  align-items: center;
  margin-top: 125px;
}

.sidebar__option {
  font-size: 1.25rem;
  padding: 10px;
  display: block;
  color: #a5def1;
}
.sidebar__option:hover {
  color: #a5def1;
  cursor: pointer;
}

.sidebar__option:visited {
  text-decoration: none;
}

.sidebar__socmed {
  margin-top: 135px;
  display: flex;
  justify-content: space-evenly;
}

.sidebar__socmedOption > span:hover {
  color: #a5def1;
  cursor: pointer;
}

.sidebar__copyright {
  margin-top: 145px;
}

@media (max-width: 500px) {
  .sidebar {
    font-size: 12px;
    width: 100vw;
    height: 100vh;
    padding-bottom: 50px;
  }

  .sidebar__title {
    margin-top: 25px;
    font-size: 24px;
  }

  .sidebar__image {
    margin-top: 50px;
    width: 120px;
  }

  .sidebar__options {
    align-items: center;
    margin-top: 10px;
    font-size: 10px;
  }

  .sidebar__socmed {
    margin-top: 60px;
  }

  .sidebar__copyright {
    margin-top: 40px;
  }
}

@media (max-width: 400px) {
  .sidebar {
    padding-bottom: 25px;
  }

  .sidebar__title {
    font-size: 0.75rem;
  }

  .sidebar__image {
    margin-top: 20px;
  }

  .sidebar__option {
    font-size: 14px;
  }
}

